body {
  font-size: 2rem;
  margin: 20px 30px;
  font-family: 'Josefin Sans';
  color: white;
  background: #FFA17F;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #00223E, #FFA17F);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#00223E), to(#FFA17F));
  background: -webkit-linear-gradient(right, #00223E, #FFA17F);
  background: -o-linear-gradient(right, #00223E, #FFA17F);
  background: linear-gradient(to left, #00223E, #FFA17F); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

#logo {
  width: 20%;
  height: auto%;
  margin: 20px auto;
}

svg {
    padding: 20px;
    height: 256px;
    width: 256px;
    stroke-width: 1;
}

.title h1 {
  text-align: center;
  margin: 20px;
}

.astros {
  font-family: 'Open Sans';
	background-color:rgba(0,0,0,0.3);	border-radius: 10px;
	width: 60%;
	height: auto;
	margin-left: auto;
	margin-right:auto;
  padding: 20px;
}

#count {
	text-align: center;
	margin: auto 100px;
}

#count-button {
  font-family: 'Open Sans';
  display:block; /* change this from inline-block */
  width:25%; /* setting the width */
  margin:0 auto; /* this will center  it */
  border-radius: 10px;
  font-size: 20px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  border-style: solid;
  cursor: pointer;
  border-width: 0;
}

#count-button:hover {
  background: #6C7A89;
  text-decoration: none;
}

ul {
  text-align: center;
  list-style: none;
}



